* {
  box-sizing: border-box;
}

.page {
  font-family: "Inter", sans-serif;
  scroll-behavior: smooth;

  &__body {
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 120px;

    &-title {
      margin-bottom: 48px;
      font-size: 32px;
      line-height: 32px;
      letter-spacing: -1.5px;
      color: $primary-color;
      text-align: center;

      @include onTablet {
        margin-bottom: 64px;
        font-size: 48px;
        line-height: 48px;
      }
    }
  }
}
